<template>
	<el-form :model="dataForm" :rules="rules">
		<el-form-item label="" prop="email">
			<el-input type="text" v-model="dataForm.email" placeholder=""></el-input>
		</el-form-item>
		<el-form-item label="" prop="password">
			<el-input type="password" v-model="dataForm.password" placeholder=""></el-input>
		</el-form-item>
		<el-button @click="login"></el-button>
	</el-form>
</template>

<script>
  export default {
    name: "Login",
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else if (value.length < 6) {
          callback(new Error('6'))
        } else {
          if (this.forgetForm.repeat !== '') {
            this.$refs.forgetForm.validateField('repeat')
          }
          callback()
        }
      }
      var validateRepeat = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else if (value !== this.forgetForm.password) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      return {
        dataForm: {name: '', password: '', email: ''},
        rules: {
          username: [{required: true, message: '', trigger: 'blur'}],
          password: [{required: true, message: '', trigger: 'blur'}],
          email: [{required: true, message: '', trigger: 'blur'}],
        },
        active: 0,
      }
    },
    methods: {
      login () {
        var params = new URLSearchParams();
        params.append('email', this.dataForm.email);
        params.append('password',this.dataForm.password);
				this.axios.post('/user/login', params)
          .then((res)=>{
            console.log(res.data.code)
            if (res.data.code === "0") {
              const h = this.$createElement;
              this.$notify({
                title: '',
                message: h('i', { style: 'color: teal'}, '')
              });
              sessionStorage.setItem("accountId", res.data.id)
              sessionStorage.setItem("email", this.dataForm.email)
              this.$router.push("/homepage");
            } else if(res.data.code === "1" || res.data.code === "2"){
              let remindTitle = ''
              let remindContent = ''
              if (res.data.code === 2) {
                remindContent = ''
              }
              this.$alert(remindContent, remindTitle, {
                confirmButtonText: ''
              });
            } else {
              sessionStorage.setItem("accountId", res.data.id)
              sessionStorage.setItem("email", this.dataForm.email)
              sessionStorage.setItem("admin", "true")
              this.$router.push('/admin/message')
              const h = this.$createElement;
              this.$notify({
                title: '',
                message: h('i', { style: 'color: teal'}, '')
              });
            }
          })
          .catch((res)=>{
            console.log(res.message);
          });
      },
    }
  }
</script>
<style scoped>
  .el-form-item {
    margin-top: 0;
  }
</style>



